import React from 'react';
import './MedalTooltip.scss';

function MedalTooltip ({ countToNext, currentMedal, nextMedal }) {
    if (countToNext === 0) {
        return (
            <div className='tooltip'>
                <p className='highest-level'><span>Congratulations!</span> you are awesome, have reached the highest level</p>
            </div>
        )
    }
    
    const currentLevel = () => (currentMedal !== 'noGiven' && currentMedal !== 'noReceived') ? (
        <React.Fragment>
            <p className='text'>You currently earned a</p>
            <p className='current-level'>{currentMedal} badge</p>
        </React.Fragment>
    ) : '';

    const nextLevel = () => (
        <React.Fragment>
            <p className='count'>{countToNext}</p>
            <p className="text">{countToNext > 1 ? 'more nominations to' : 'more nomination to'}</p>
            <p className="next-level">{`${nextMedal} badge`}</p>
        </React.Fragment>
    );


    return (
        <div className='tooltip'>
            {currentLevel()}
            {nextLevel()}
        </div>
    );
};

export default MedalTooltip;
