import moment from 'moment';
import Cookies from 'universal-cookie';

const cookie = new Cookies();
const host = process.env.REACT_APP_BACKEND_HOST;

export const login = () => document.cookie.indexOf('login=') === -1 ? (
        fetch(`${host}/auth/`,{ method: 'GET' })
       .then(response => response.json())
       .then(response => window.location.href = response.authurl)
       .catch((err) => console.log("Error trying to log, please try again",err))
) : null;

export const setCookie = (value) => {
    const DATE = new Date(moment().add(1,'hour').format());
    cookie.set("login", value, { path: '/' , expires: DATE });
};
