import React from 'react';
import { Button } from 'react-materialize';
import { fromSnakeToTitleCase } from '../../../../utils/transformations';
import { colorClassForValue } from './constants';
import './Values.scss';

function Values({
    onValueSelection,
    selectedValue,
    valuesList
}) {
    if(!Array.isArray(valuesList) || valuesList.length === 0) {
        return;
    }

    const getValues = valuesList.map((value) => {
        const valueClass = `value ${value === selectedValue ? `active ${colorClassForValue[value]}` : 'default-color'}`;
    
        return (
            <Button
                className={valueClass}
                id={value}
                key={`value-button-${value}`}
                onClick={(event) => onValueSelection(event.target.value)}
                value={value}
                waves='light'
            >
                {fromSnakeToTitleCase(value)}
            </Button>  
        );
    });

    return (
        <div className='values-container'>
            <p className="title">Choose a Value</p>
            {getValues}
        </div>
    );
};

export default Values;
