import React, { Component } from 'react';
import { Modal, Row, Col, ProgressBar} from 'react-materialize';
import NavItem from '../navItem/navItem';
import ProfileModal from '../ProfileModal/ProfileModal';

import Cookies from 'universal-cookie';
const cookie = new Cookies();

class MyActivity extends Component {

  constructor(props) {
    super(props);
    this.state = {
      nominations_sent: [],
      nominations_received: [],
      page_loaded: false
    }
    this.fetchData = this.fetchData.bind(this);
    this.clearData = this.clearData.bind(this);
  }

  fetchData() {
    const host = process.env.REACT_APP_BACKEND_HOST;
    fetch(`${host}/form`)
      .then(response => response.json())
      .then(data => {
        let nominations_received = data.filter(nominations_received => `${nominations_received.nomination.nomineeInfo.name.user}@apexsystems.com` == cookie.get("login").email);
        let nominations_sent = data.filter(nominations_sent => `${nominations_sent.nomination.nominatorInfo.name.user}@apexsystems.com` == cookie.get("login").email);
        this.setState({
          nominations_received,
          nominations_sent,
          page_loaded: true
        })
      });
  }
  clearData() {
    this.setState({
      nominations_sent: [],
      nominations_received: [],
      page_loaded: false
    });
  }

  render() {
    const { page_loaded, nominations_sent, nominations_received  } = this.state; 
    
    return (
        <Modal
          id='awardModal'
          header='Awards'
          modalOptions={{ ready: this.fetchData, complete: this.clearData }}
          trigger={<NavItem>My Activity</NavItem>}>
          {page_loaded ? <ProfileModal sent={nominations_sent} received={nominations_received} /> : <Row> <Col s={12}> <ProgressBar /> </Col> </Row>}
        </Modal>  
    );
  }
}

export default MyActivity;
