import React, { Component } from 'react';
import { Row, Col, Tabs, Tab } from 'react-materialize';
import ProfileTable from '../profileTable/profileTable';
import './profile.css';

export default class ProfileModal extends Component {

  render() {
    return (
      <Row className="tabs-container">
        <Col s={12} m={12}>
          <Tabs className='z-depth-1 awards-tabs'>
            <Tab title="Given" active>
              <Row className='tab-container'>
                <Col s={12} m={12}>
                  <ProfileTable awards={this.props.sent} type={'Given'} />
                </Col>
              </Row>
            </Tab>
            <Tab title="Received">
              <Row className='tab-container'>
                <Col s={12} m={12}>
                  <ProfileTable awards={this.props.received} type={'Received'} />
                </Col>
              </Row>
            </Tab>
          </Tabs>
        </Col>
      </Row>
    );
  }
}
