import React, { useEffect } from 'react'
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { base } from './utils/constants';
import { login } from './utils/login';

//Old Components (To be removed when new components are finished)
import Auth from './components/auth/auth';
import Reports from './components/reports/reports';

//New Components
import HomePage from './components/newComponents/Home/Home';
import NavBar from './components/newComponents/NavBar/NavBar';
import NominationForm from './components/newComponents/Nomination/Nomination';
import RouteNotFound from './components/newComponents/RouteNotFound/RouteNotFound';

function App() {
    useEffect(() => {
        login();
    },[]);
     
    return (
        <BrowserRouter basename={base}>
            <div className="app">
                <NavBar/> 
                <div id="app-content">
                    <Switch>
                        <Route exact path="/" component={HomePage}/>
                        <Route exact path="/auth" component={Auth} />
                        <Route exact path="/stats" component={Reports} />
                        <Route exact path="/nominate" component={NominationForm} />
                        <Route component={RouteNotFound} />
                    </Switch>  
                </div>
            </div>
        </BrowserRouter>   
    );
}

export default App;
