import React, { Component } from 'react';
import { Row, Col, Button } from 'react-materialize';
import ReactTable from "react-table";
import moment from 'moment';
import './reportTable.css';
import besrvc from '../../assets/images/BEINSERVICE.svg';
import beau from '../../assets/images/BEAUTHENTIC.svg';
import beac from '../../assets/images/BEACCOUNTABLE.svg';
import briex from '../../assets/images/BRINGEXCELLENCE.svg';

class ReportTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: this.props.data
    }
  }

  getSvgCoreValue(valuecode) {
    switch (valuecode) {
      case 'do_the_right_thing':
        return beau;
      case 'will_to_win':
        return beac;
      case 'make_others_better':
        return besrvc;
      case 'professionalism':
        return briex;
      default:
        return besrvc;
    }
  }

  render() {
    const data = this.state.data;
    const setValues = data.map(values => {
      const date = moment(values.nomination.date).format('LL');

      return {
        date: date,
        value: <img className="core-value-img" alt={values.nomination.awardInfo.coreValue} src={this.getSvgCoreValue(values.nomination.awardInfo.coreValue)} />,
        nominee: values.nomination.nomineeInfo.name.displayName,
        nominee_manager: values.nomination.nomineeInfo.manager.displayName,
        nominee_city: values.nomination.nomineeInfo.location,
        nominator: values.nomination.nominatorInfo.name.displayName,
        nominator_manager: values.nomination.nominatorInfo.manager.displayName,
        nominator_city: values.nomination.nominatorInfo.location
      };
    });

    return (
      <Row className="reportTable">
        <Col s={12}>
          <h1>Data</h1>
        </Col>
        <ReactTable
          data={setValues}
          columns={[
            {
              Header: "General",
              columns: [
                {
                  Header: "Date",
                  accessor: "date"
                },
                {
                  Header: "Value",
                  accessor: "value"
                }
              ]
            },
            {
              Header: "Nominee Info",
              columns: [
                {
                  Header: "Name",
                  accessor: "nominee"
                },
                {
                  Header: "Manager",
                  accessor: "nominee_manager"
                },
                {
                  Header: "Region",
                  accessor: "nominee_city"
                }
              ] 
            },
            {
              Header: "Nominator Info",
              columns: [
                {
                  Header: "Name",
                  accessor: "nominator"
                },
                {
                  Header: "Manager",
                  accessor: "nominator_manager"
                },
                {
                  Header: "Region",
                  accessor: "nominator_city"
                }
              ] 
            }
          ]}
          defaultPageSize={10}
          showPageSizeOptions={false}
          className="-striped -highlight"
        />
        <Col s={12} className="exportExcel">
          <Button waves='light' node='a' href={process.env.REACT_APP_BACKEND_HOST + "/stats/excel?" + this.props.query}>Export to Excel</Button>
        </Col>
      </Row>
    );
  }
}

export default ReportTable
