import Cookies from 'universal-cookie';

const cookie = new Cookies();
const host = process.env.REACT_APP_BACKEND_HOST;

export const getAwardsInformation = () => {
    return fetch(`${host}/user/gamification/${cookie.get("login").username}`, {
        headers: {
            'Accept': 'application/json',
            'Authorization': `Bearer ${cookie.get("login").token}`,
            'Content-Type': 'application/json'
        },
        method: 'GET'
    }).then(response => response.json())
    .then(awardsInfo => awardsInfo)
    .catch((error) => console.log("Error retrieving awards information, please try again", error.message));
};
