import React, { useState } from 'react';
import { Input } from 'react-materialize';
import FormButtons from './FormButtons/FormButtons';
import Values from './Values/Values';
import { DEFAULT_VALIDATIONS, VALUES } from './constants';
import { validateField, sendNomination } from './helpers';
import './Nomination.scss';

function NominationForm({ history }) {
    const [message, setMessage] = useState('');
    const [nomineeName, setNomineeName] = useState('');
    const [selectedValue, setSelectedValue] = useState('will_to_win');
    const [validField, setValidField] = useState(DEFAULT_VALIDATIONS);

    const clearForm = () => {
        setMessage('');
        setNomineeName('');
        setSelectedValue('will_to_win');
        setValidField(DEFAULT_VALIDATIONS);
    }

    const handleBlur = async (id, value) => {
        if (value === '') {
            return;
        }

        const formObject = { ...validField }
        const validation = await validateField[id](value) || [false, 'Unknown field, no validation performed'];
        const validatedField = validation[0];
        const errorMessage = validation[1];;
        
        Object.assign(formObject, {
            [id]: {
                error: errorMessage,
                valid: validatedField
            } 
        });
        setValidField(formObject);
    };

    const sendForm = async (option) => {
        const isFormValid = validateForm();

        if (isFormValid) {
            const wasSuccessfullySent = await sendNomination(nomineeName, message, selectedValue);
            if (wasSuccessfullySent) {
                window.Materialize.toast(
                    'Your Nomination has been <span>successfully sent</span><i class="material-icons">check</i>', 
                    4000, 
                    'success-toast'
                );

                (option === 'send') ? history.push('/') : clearForm();
            }
        }
    }

    const validateForm = () => {
        let isFormValid = true;

        Object.keys(validField).forEach((field) => {
            if(!validField[field].valid) {
                isFormValid = false;
            }
        });

        if (!isFormValid) {
            window.Materialize.toast(
                'You have information to be completed, please properly fill it before sending your nomination <i class="material-icons">info</i>', 
                4000, 
                'info-toast'
            );
        }

        return isFormValid;
    }

    return (
        <div className='nomination-form'>
            <div className='nomination-header'>
                <h1>New Nomination</h1>
            </div>
            <div className='nomination-form-container'>
                <Values
                    onValueSelection={setSelectedValue}
                    selectedValue={selectedValue}
                    valuesList={VALUES}
                />
                <div className='fields-container'>
                    <div className='email-field'>
                        <Input
                            id='nomineeName'
                            error={validField['nomineeName'].error}
                            label='Nominee Email*'
                            onBlur={(event) => handleBlur(event.target.id, event.target.value)}
                            onChange={(event) => setNomineeName(event.target.value)}
                            placeholder='email'
                            type='text'
                            value={nomineeName}
                        >
                            <span>@apexsystems.com</span>
                        </Input>
                    </div>
                    <Input
                        id='message'
                        error={validField['message'].error}
                        label='Your message for the nominee*'
                        maxLength='255'
                        onBlur={(event) => handleBlur(event.target.id, event.target.value)}
                        onChange={(event) => setMessage(event.target.value)}
                        type='textarea'
                        value={message}
                    />
                    <FormButtons
                        sendForm={sendForm}
                    />
                </div>
            </div>
        </div>
    );
};

export default NominationForm;
