import Cookies from 'universal-cookie';

const cookie = new Cookies();
const host = process.env.REACT_APP_BACKEND_HOST;

//Send Nomination
export async function sendNomination(nomineeName, message, selectedValue) {

    return fetch(`${host}/form`, {
        method: 'POST',
        headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization': `${cookie.get("login").token}`
        },
        body: JSON.stringify({
            "nomination": {
                "awardInfo": {
                    "coreValue": selectedValue,
                    "reason": message
                },
                "nomineeInfo": {
                    "name": nomineeName
                }
            }
        }),
        mode: 'cors'
    }).then((response) => response.status === 200 ? true : false)
    .catch((error) => console.log("Error sending form to server, please try again", error.message));
};

//Validators
export async function validateEmail(email) {
    const emailAddress = email.toLowerCase();
    
    const status = await fetch(`${host}/user/${emailAddress}`, {
        method: 'GET',
        headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization': `${cookie.get("login").token}`
        },
        mode: 'cors'
    }).then((response) => response.status)
    .catch((error) => console.log("Error validating email, please try again", error.message));

    switch(status) {
        case 200:
            return [true, ''];
        case 401: 
            return [false, getErrorMessage['nomineeName']];
        case 403: 
            return [false, getErrorMessage['noAutoNomination']];
        default:
            return [false, 'errorInRequest']
    }
};

export const validateMessage = (message) => 
    message.length >= 5 && message.length <=255 ? [true, ''] : [false, getErrorMessage['message']];

//Dictionary of messages
export const getErrorMessage = {
    'nomineeName': 'The email provided is not valid or is not part of the organization',
    'message': 'The minimum lenght is 15 characters and maximum is 255',
    'noAutoNomination': 'You can not nominate yourself, please try with something more ethic',
    'errorInRequest': 'The email validator is experiencing issues, please try again, if the issue continues contact the administrators'
};

//Dictionary of validators
export const validateField = {
    'nomineeName': async (email) => await validateEmail(email),
    'message': (message) => validateMessage(message)
};
