import React, { Component } from 'react';
import { Row, Col, ProgressBar } from 'react-materialize';
import moment from 'moment';
import Cookies from 'universal-cookie';
import Filters from '../filters/filters';
import ReportTable from '../reportTable/reportTable';
import './reports.css';
import { Redirect } from 'react-router';
import { login } from '../../utils/login';

const COOKIE = new Cookies();

function initializeDateRange(value) {
  let quarter = moment().quarter();
  let range = {};
  let initialDate = '';
  let finalDate = '';
  const YEAR = new Date().getFullYear();
  switch (quarter) {
    case 1:
      initialDate = moment(YEAR + '-01-01').format('LL');
      finalDate = moment(YEAR + '-03-31').format('LL');
      break;
    case 2:
      initialDate = moment(YEAR + '-04-01').format('LL');
      finalDate = moment(YEAR + '-06-30').format('LL');
      break;
    case 3:
      initialDate = moment(YEAR + '-07-01').format('LL');
      finalDate = moment(YEAR + '-09-30').format('LL');
      break;
    case 4:
      initialDate = moment(YEAR + '-10-01').format('LL');
      finalDate = moment(YEAR + '-12-31').format('LL');
      break;
    default:
      console.log("Not valid date");
      break;
  }
  range = { initialDate, finalDate };
  if (value === 'initialDate') {
    return range.initialDate;
  }
  else if (value === 'finalDate') {
    return range.finalDate;
  }
}

class Reports extends Component {
  constructor(props) {
    super(props);
    this.state = {
      initialDate: initializeDateRange('initialDate'),
      finalDate: initializeDateRange('finalDate'),
      cityGiven: '',
      cityReceived: '',
      managerGiven: '',
      managerReceived: '',
      corevalue: '',
      loaded: false,
      data: [],
      query: ''
    };
    this.dataRangeChange = this.dataRangeChange.bind(this);
    this.filterChange = this.filterChange.bind(this);
    this.clearFilters = this.clearFilters.bind(this);
    this.applyFilters = this.applyFilters.bind(this);
  }

  dataRangeChange(event, type) {
    event.preventDefault();
    var date = event.target.value;
    if (date !== '') {
      date = date.split(' ');
      date[1] = date[1].slice(0, -1);
      date = date[1] + ' ' + date[0] + ', ' + date[2];
    }
    else {
      date = '';
    }

    if (type === 'Initial') {
      this.setState({
        initialDate: date
      });
    }
    else if (type === 'Final') {
      this.setState({
        finalDate: date
      });
    }
  }

  filterChange(event, type) {
    event.preventDefault();
    const VALUE = event.target.value;

    switch (type) {
      case 'cityGiven':
        this.setState({
          cityGiven: VALUE
        });
        break;
      case 'cityReceived':
        this.setState({
          cityReceived: VALUE
        });
        break;
      case 'level':
        this.setState({
          level: VALUE
        });
        break;
      case 'managerGiven':
        this.setState({
          managerGiven: VALUE
        });
        break;
      case 'managerReceived':
        this.setState({
          managerReceived: VALUE
        });
        break;
      case 'coreValue':
        this.setState({
          corevalue: VALUE
        });
        break;
      default:
        console.log(
          "VALUE TYPE NOT RECOGNIZED \n",
          "Type Received: " + type + "\n",
          "Value Received: " + VALUE);
        break;
    }
  }

  clearFilters() {
    this.setState({
      initialDate: initializeDateRange('initialDate'),
      finalDate: initializeDateRange('finalDate'),
      cityGiven: '',
      cityReceived: '',
      managerGiven: '',
      managerReceived: '',
      corevalue: '',
      loaded: false,
      data: []
    });
    this.applyFilters();
  }

  applyFilters() {
    this.setState({
      loaded: false
    });

    const {
      initialDate,
      finalDate,
      cityGiven,
      cityReceived,
      managerGiven,
      managerReceived,
      corevalue,
      loaded
    } = this.state;

    const host = process.env.REACT_APP_BACKEND_HOST;
    let query = '';

    if (initialDate !== '') {
      if (query !== '') {
        query += "&";
      }
      query += "from=" + moment(initialDate).format('YYYY-MM-DD');
    }
    if (finalDate !== '') {
      if (query !== '') {
        query += "&";
      }
      query += "to=" + moment(finalDate).format('YYYY-MM-DD');
    }
    if (cityGiven !== '') {
      if (query !== '') {
        query += "&";
      }
      query += "citygiven=" + cityGiven;
    }
    if (cityReceived != '') {
      if (query !== '') {
        query += "&";
      }
      query += "cityreceived=" + cityReceived;
    }
    if (managerGiven != '') {
      if (query !== '') {
        query += "&";
      }
      query += "managergiven=" + managerGiven;
    }
    if (managerReceived != '') {
      if (query !== '') {
        query += "&";
      }
      query += "managerreceived=" + managerReceived;
    }
    if (corevalue != '') {
      if (query !== '') {
        query += "&";
      }
      query += "value=" + corevalue;
    }
    this.setState({
      query: query
    });

    return fetch(`${host}/stats?${query}`, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(nomination => {
        this.setState({
          data: nomination,
          loaded: true
        });
      })
      .catch((json) => {
        console.log("Error applying filters, please try again");
      });
  }

  UNSAFE_componentWillMount() {
    if (COOKIE.get("login") === undefined) {
      login();
    }
    this.applyFilters();
  }

  render() {
    if (this.state.loaded === false) {
      return (
        <div>
          <ProgressBar />
        </div>
      );
    }
    else if (COOKIE.get("login").usertype === 'normal') {
      return (<Redirect to="/" />);
    }
    else {
      return (
        <div>
          <Row className="reportsHeader">
            <Col s={12}>
              <h1>Stats</h1>
            </Col>
          </Row>
          <Row>
            <Col s={12} m={3}>
              <Filters dataRangeChange={this.dataRangeChange} filterChange={this.filterChange} data={this.state} clearFilters={this.clearFilters} applyFilters={this.applyFilters} />
            </Col>
            <Col s={12} m={9}>
              <ReportTable data={this.state.data} query={this.state.query} applyFilters={this.applyFilters} />
            </Col>
          </Row>
        </div>
      );
    }
  }
}

export default Reports