import React, { Component } from 'react';
import { Table } from 'react-materialize';
import moment from 'moment';
import '../profileTable/profileTable.css';
import besrvc from '../../assets/images/BEINSERVICE.svg';
import beau from '../../assets/images/BEAUTHENTIC.svg';
import beac from '../../assets/images/BEACCOUNTABLE.svg';
import briex from '../../assets/images/BRINGEXCELLENCE.svg';
import medalGray from '../../assets/images/medalGray.png';

const YEAR = new Date().getFullYear();

export default class ProfileTable extends Component {

  constructor(props) {
    super(props)
    this.getCoreValue = this.getCoreValue.bind(this);
  }

  getCoreValue(valuecode) {
    switch (valuecode) {
      case 'beaccountable':
        return beac;
      case 'beauthentic':
        return beau;
      case 'beinservice':
        return besrvc;
      case 'bringexcellence':
        return briex;
      default:
        return besrvc;
    }
  }

  getDiploma(type,id) {
    const host = process.env.REACT_APP_BACKEND_HOST;
    const diplomaURl = `${host}/form/diploma/${id}`;
    if(this.props.type === "Received") 
      if(type === "th")
        return <th className="diploma" data-field="diploma">Diploma</th>
      else
      return <td className="diploma"><a href={diplomaURl}><i class="material-icons">movie_filter</i></a></td>
  }

  render() {

    if (this.props.awards.length === 0) {
      return (
        <div className="no_awards">
          {this.props.type === "Given" ? <h2> No Awards given yet for <span>{YEAR}</span></h2> : <h2> No Awards received yet for <span>{YEAR}</span></h2>}
          <img src={medalGray} alt='medal' />
        </div>
      )
    }

    return (
      <div>
        <Table className="my_activity">
          <thead>
            <tr>
              <th className="date" data-field="date">Date</th>
              {this.props.type === "Given" ? <th className="name" data-field="name">Nominee</th> : <th data-field="name">Nominator</th>}
              <th className="value" data-field="value">Value</th>
              <th className="reason" data-field="reason">Reason</th>
              {this.getDiploma("th")}
            </tr>
          </thead>
          <tbody>
            {this.props && this.props.awards && this.props.awards.map(item =>
              <tr key={item._id}>
                <td className="date">{moment(new Date(item.nomination.date)).format('MMMM Do, YYYY')}</td>
                {this.props.type === "Given" ? <td className="name">{item.nomination.nomineeInfo.name.displayName}</td> : <td className="name">{item.nomination.nominatorInfo.name.displayName}</td>}
                <td className="value"><img className="core_value" alt={item.nomination.awardInfo.coreValue} src={this.getCoreValue(item.nomination.awardInfo.coreValue)} /></td>
                <td className="reason">{item.nomination.awardInfo.reason}</td>
                {this.getDiploma("td",item._id)}
              </tr>
            )}
          </tbody>
        </Table>
      </div>
    )
  }
}