import React from 'react';
import { kudosBadgesNamesAndLegends } from './constants';
import './KudosBadges.scss';

import ace from '../../../../assets/images/kudos/ace.svg';
import club from '../../../../assets/images/kudos/club.svg';
import diamond from '../../../../assets/images/kudos/diamond.svg';
import novice from '../../../../assets/images/kudos/novice.svg';
import star from '../../../../assets/images/kudos/star.svg';

const badgesList = {
    ace: ace,
    club: club,
    diamond: diamond,
    novice: novice,
    star: star
};

function KudosBadges ({ awardsInformation }) {
    if (!awardsInformation) {
        return null;
    }

    const kudosBadges = awardsInformation.kudos;
    const badges = Object.keys(badgesList).map((badge) => (
        <div className='tooltip-wrapper' key={`tooltip-kudos-${badge}`}>
            <img
                alt={`kudos-${badge}-badge`}
                className={kudosBadges.indexOf(badge) !== -1 ? 'kudos-badge active-badge' : 'kudos-badge'}
                src={badgesList[badge]} 
            />
            <div className='tooltip'>
                <span>{kudosBadgesNamesAndLegends[badge].name}: </span>{kudosBadgesNamesAndLegends[badge].legend}
            </div>
        </div>
    ));

    return (
        <div className='kudos-badges-container'>
            <p className='title'>My Kudos Badges</p>
            {badges}
        </div>
    );
};

export default KudosBadges;
