import React from 'react';
import './Medal.scss';

// Medal SVGs
import blue from '../../../../../assets/images/awards/blue.svg';
import bronze from '../../../../../assets/images/awards/bronze.svg';
import captain from '../../../../../assets/images/awards/captain.svg';
import champion from '../../../../../assets/images/awards/champion.svg';
import cheerleader from '../../../../../assets/images/awards/cheerleader.svg';
import coach from '../../../../../assets/images/awards/coach.svg';
import contributor from '../../../../../assets/images/awards/contributor.svg';
import gold from '../../../../../assets/images/awards/gold.svg';
import noGiven from '../../../../../assets/images/awards/noGiven.svg';
import noReceived from '../../../../../assets/images/awards/noReceived.svg';
import silver from '../../../../../assets/images/awards/silver.svg';
import platinum from '../../../../../assets/images/awards/platinum.svg';

// Medal Dictionary (name: path)
const medalList = {
  noGiven: noGiven,
  noReceived: noReceived,
  blue: blue,
  bronze: bronze,
  silver: silver,
  gold: gold,
  platinum: platinum,
  contributor: contributor,
  cheerleader: cheerleader,
  coach: coach,
  captain: captain,
  champion: champion
};

function Medal ({ count, medal, title }) {
    return (
        <div className='medal-container'>
            <p className='title'>{title}: <span className="count">{count}</span></p>
            <img className='medal-image' src={medalList[medal]}/>
        </div>
    );
};

export default Medal;
