import React from 'react';
import { ProgressBar } from 'react-materialize';
import Medal from './Medal/Medal';
import MedalTooltip from './MedalTooltip/MedalTooltip'
import './MedalsContainer.scss';

function MedalsContainer ({ awardsInformation }) {
    if (!awardsInformation) {
        return (
            <div className='medals-container'>
                <ProgressBar />
            </div>
        );
    }

    const awardedData = awardsInformation.badges.awarded;
    const earnedData = awardsInformation.badges.earned;
    const nextAwardedData = awardsInformation.nextBadges.awarded;
    const nextEarnedData = awardsInformation.nextBadges.earned;

    return (
        <div className='medals-container'>
            <div className='medal-tooltip-wrapper'>
                <Medal
                    count={awardedData.count}
                    medal={awardedData.medal}
                    title='Awards Given'
                />
                <MedalTooltip
                    countToNext={nextAwardedData.count}
                    currentMedal={awardedData.medal}
                    nextMedal={nextAwardedData.next}
                />
            </div>
            <div className='medal-tooltip-wrapper'>
                <Medal
                    count={earnedData.count}
                    medal={earnedData.medal}
                    title='Awards Received'
                />
                <MedalTooltip
                    countToNext={nextEarnedData.count}
                    currentMedal={earnedData.medal}
                    nextMedal={nextEarnedData.next}
                />
            </div>
        </div>
    );
};

export default MedalsContainer;
