import React, { useEffect, useState } from 'react';
import { Button } from 'react-materialize';
import { Link } from 'react-router-dom';
import KudosBadges from './KudosBadges/KudosBadges';
import MedalsContainer from './MedalsContainer/MedalsContainer';
import { getAwardsInformation } from './helpers';
import './Home.scss';

function HomePage() {
    const [awardsInformation, setAwardsInformation] = useState(null);

    const getAwardsData = async () => {
        const awardsData = await getAwardsInformation();
        setAwardsInformation(awardsData);
    }

    useEffect(() => {
        getAwardsData();
    },[]);

    return (
        <div className='home-page'>
            <div className='home-header'>
                <h1>Apex Core Values Awards</h1>
                <Link to="/nominate">
                    <Button waves='light'>Nominate Now</Button>
                </Link>
            </div>
            <div className='home-container'>
                <MedalsContainer awardsInformation={awardsInformation} />
                <KudosBadges awardsInformation={awardsInformation} />
            </div>
        </div>
    );
};

export default HomePage;
