import React, { Component } from 'react'
import { Row, Col, Input, Button } from 'react-materialize'
import './filters.css';

class Filters extends Component {

  constructor(props) {
    super(props);
      this.state = {
        initialDate: this.props.data.initialDate,
        finalDate: this.props.data.finalDate,
        cityGiven: this.props.data.cityGiven,
        cityReceived: this.props.data.cityReceived,
        managerGiven: this.props.data.managerGiven,
        managerReceived: this.props.data.managerReceived,
        corevalue: this.props.data.corevalue
      };
  }

  render() {
    const {
      initialDate,
      finalDate, 
      cityGiven,
      cityReceived,
      managerGiven,
      managerReceived,
      corevalue 
    } = this.props.data;

    return (
      <div>  
        <Row className="filters">
          <Col s={12}>
            <h1>Filters</h1>
          </Col>
          <Row className="rangeValues">
              <Col s={12}>
                  <h2>Date</h2>
              </Col>
              <Col s={12}>
                <h6 s={2} className="rangeLabel">From</h6>
                <Input s={10} name='on' type='date' className='datepicker' placeholder={initialDate} value={initialDate} onChange={(e) => this.props.dataRangeChange(e,"Initial")} />
              </Col>
              <Col s={12}>
                <h6 s={2} className="rangeLabel">To</h6>
                <Input s={10} name='on' type='date' className='datepicker' placeholder={finalDate} value={finalDate} onChange={(e) => this.props.dataRangeChange(e,"Final")}/>
              </Col>
          </Row>
          <Row>
              <Col s={12} className="selectFilter">
                <Input s={12} label="From Region" type="select" value={cityGiven} onChange={(e) => this.props.filterChange(e,"cityGiven")}>
                  <option value=''>All Business Regions</option>
                  <option value='central'>Central</option>
                  <option value='east'>East</option>
                  <option value='west'>West</option>
                  <option value='MDC'>MDC</option>
                </Input>
              </Col>
          </Row>
          <Row>
              <Col s={12} className="selectFilter">
                <Input s={12} label="To City" type="select" value={cityReceived} onChange={(e) => this.props.filterChange(e,"cityReceived")}>
                  <option value=''>All Business Regions</option>
                  <option value='central'>Central</option>
                  <option value='east'>East</option>
                  <option value='west'>West</option>
                  <option value='MDC'>MDC</option>
                </Input>
              </Col>
          </Row>
          <Row>
              <Col s={12} className="selectFilter">
                <Input s={12} label="Intersys Values" type="select" value={corevalue} onChange={(e) => this.props.filterChange(e,"coreValue")}>
                  <option value=''>All Core Values</option>
                  <option value='will_to_win'>Will To Win</option>
                  <option value='do_the_right_thing'>Do The Right Thing</option>
                  <option value='make_others_better'>Make Other Better</option>
                  <option value='professionalism'>Professionalism</option>
                </Input>
              </Col>
          </Row>
          <Row>
              <Col s={12} className="selectFilter">
                <Input s={12} label="Nominator Manager" type="text" value={managerGiven} onChange={(e) => this.props.filterChange(e,"managerGiven")} />
              </Col>
          </Row>
          <Row>
              <Col s={12} className="selectFilter">
                <Input s={12} label="Nominee Manager" type="text" value={managerReceived} onChange={(e) => this.props.filterChange(e,"managerReceived")} />
              </Col>
          </Row>
          <Row className="filtersButtons">
              <Col s={12}>
                <Button waves='light' className="clear" onClick={() => this.props.clearFilters()}>Clear</Button>
                <Button waves='light' className="apply" onClick={() => this.props.applyFilters()}>Apply Filters</Button>
              </Col>
          </Row>
        </Row>   
      </div>
    )
  }
}

export default Filters