import React, { useEffect, useState } from 'react';
import { Icon, Dropdown, Navbar, NavItem } from 'react-materialize';
import { Link } from 'react-router-dom';
import Cookies from 'universal-cookie';
import MyActivity from '../../MyActivity/MyActivity';
import { login } from '../../../utils/login';
import Logo from '../../../assets/images/logos/apex-logo.svg';
import './NavBar.scss';

const cookie = new Cookies();

const RouterNavItem = ({ href, children }) => (<li><Link to={ href }>{ children }</Link></li>);

function NavBar() {
    const [loginData, setLoginData] = useState({});

    useEffect(() => {
        setLoginData(cookie.get('login'));
    }, []);

    const logo = (
        <Link to="/">
            <img href="/" className="logo" src={Logo} />
        </Link>
    );

    const adminNav = () =>
        loginData && loginData.usertype === 'admin' ? 
            <RouterNavItem href='/stats'>Stats</RouterNavItem> 
            : null;

    const signOutSession = (event) => {
        event.preventDefault();
        cookie.remove('login', { path: '/' });
        login();
    }

    const dropdownNav = () => (
        <Dropdown trigger={
            <NavItem className='session-menu'>
                {loginData && loginData.name}
                <i className='material-icons right'>keyboard_arrow_down</i>
            </NavItem>
        }>
            <li className='help'>
                <a href='https://intersysconsulting.slack.com/app_redirect?channel=recognition-app-help' target='_blank'>
                    help<i className='material-icons right'>help</i>
                </a>
            </li>
            <li className='sign-out' onClick={(event) => signOutSession(event)}>
                <p>
                    Sign Out <i className='material-icons right'>power_settings_new</i>
                </p>
            </li>
        </Dropdown>
    );

    return (
        <Navbar brand={logo} right className='navigation-bar'>
            <RouterNavItem href='/'>
                <Icon>home</Icon>
            </RouterNavItem>
            <RouterNavItem href='/nominate'>Nominate</RouterNavItem>
            <MyActivity />
            {adminNav()}
            {dropdownNav()}
        </Navbar>
    );
}

export default NavBar;
