import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { withRouter } from 'react-router-dom';
import { setCookie } from '../../utils/login';
import actions from '../../actions';

import loader from '../../assets/images/loader.gif';
import './auth.css';

const mapStateToProps = (state => {
  return {
    isUserLogged: state.login.isUserLogged
  }
});

const mapDispatchToProps = (dispatch => {
  return { actions: bindActionCreators(actions, dispatch), dispatch }
});

class Auth extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="loader">
        <img src={loader} />
      </div>
    );
  }

  componentDidMount() {
    const {location, actions: {userLogged} } = this.props; 
    const url = location.search;
    var hash;
    var data = {};
    var hashes = url.slice(url.indexOf('?') + 1).split('&');
    for (let i = 0; i < hashes.length; i++) {
      hash = hashes[i].split('=');
      if (hash[0] == 'name') {
        hash[1] = decodeURI(hash[1]);
      }
      else if (hash[0] == 'email') {
        hash[1] = hash[1].replace('%40', '@');
      } else if (hash[0] == 'usertype') {
        hash[1] = decodeURI(hash[1]);
      }
      data[hash[0]] = hash[1];
    }
    setCookie(data);
    userLogged();
    setTimeout( this.props.history.push('/'), 10000);
  }
}

export default withRouter(connect(mapStateToProps,mapDispatchToProps)(Auth));
